  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

body {
  background: #f4f6f8;
  color: #212529;
  line-height: 1.5;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
header {
  /* background: linear-gradient(130deg, #13274c, #2e5baa); */
  background: linear-gradient(130deg, #13274c, #618bd3);
  width: 100%;
  padding: 1rem 2rem;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
  color: white;
}
#weather-form {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 1rem;
}
.left h1 {
  font-size: 2.5rem;
  font-weight: 700;
  letter-spacing: 0.5px;

  background-image: linear-gradient(
    45deg,
    #ff7f50,
    #ffb347,
    #87ceeb,
    #9370db,
    #d1e02bf0,
    #d25b4e,
    #ff7f50
  );
  background-size: 400%;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  animation: moveGradient 6s linear infinite;
}
@keyframes moveGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.right {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  flex-wrap: wrap;
}
#location {
  padding: 0.6rem 0.9rem;
  border-radius: 8px;
  border: none;
  outline: none;
  font-size: 1rem;
  min-width: 230px;
  font-family: "Montserrat", sans-serif;

  border: 3.5px solid transparent;
  background-image:
    linear-gradient(white, white), linear-gradient(to right, #fc8561, #8ff470);
  background-origin: border-box;
  background-clip: padding-box, border-box;
}
#location:focus {
  outline: none;
  background-image:
    linear-gradient(white, white), linear-gradient(to right, #8ff470, #fc8561);
}
.input-wrapper {
  position: relative;
}
#search-btn {
  background: #e59766;
  border: none;
  border-radius: 8px;
  padding: 8px 10px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition:
    transform 0.2s ease,
    background 0.3s ease;
}
#search-btn:hover {
  background: #e67348;
  transform: scale(1.06);
}
#search-btn svg {
  width: 20px;
  height: 20px;
}
.unit-toggle {
  position: relative;
  width: 70px;
  height: 34px;
}

.unit-toggle input {
  display: none;
}

.slider {
  position: absolute;
  cursor: pointer;
  background-color: #66c569;
  border-radius: 34px;
  width: 100%;
  height: 100%;
  transition: background-color 0.3s;
  display: flex;
  align-items: center;
  justify-content: space-around;
  box-sizing: border-box;
  font-size: 14px;
  font-weight: bold;
  color: #ffffff;
}
.slider::before {
  content: "";
  position: absolute;
  height: 26px;
  width: 26px;
  background-color: #2e7d32;
  color: white;
  font-size: 12px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  left: 5.15px;
  bottom: 4.5px;
  transition:
    transform 0.3s,
    background-color 0.3s;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);
}
.unit-text {
  z-index: 2;
  transition: color 0.3s;
}
.unit-text.celsius {
  color: #ffffff;
}
.unit-text.fahrenheit {
  color: rgba(255, 255, 255, 0.6);
}
.unit-toggle input:checked + .slider {
  background-color: #2196f3;
}
.unit-toggle input:checked + .slider::before {
  transform: translateX(34px);
  background-color: #1565c0;
}
.unit-toggle input:checked + .slider .unit-text.celsius {
  color: rgba(255, 255, 255, 0.6);
}
.unit-toggle input:checked + .slider .unit-text.fahrenheit {
  color: #ffffff;
}

#app {
  flex: 1;
  display: grid;
  grid-template-columns: 2fr 1fr;
  justify-content: space-around;
  width: 100%;
  gap: 24px;
  padding: 24px;
}
.welcome-message {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-column: 1/3;
  width: 100%;
  text-align: center;
  padding: 0 5px;
  color: #555;
  transition: all 0.3s ease-in-out;
}
.welcome-gif {
  width: 250px;
  height: 250px;
}
.welcome-message h2 {
  font-family: "Montserrat", sans-serif;
  font-size: 1.8rem;
  margin-bottom: 10px;
  color: #333;
}

.welcome-message p {
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
  color: #666;
}
.currentForecast {
  background-color: rgba(0, 0, 0, 0.6);
  background-blend-mode: darken;
  backdrop-filter: blur(12px);
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  font-size: 14px;
  border-radius: 10px;
  transition: background-image 0.6s ease-in-out;
}
.location {
  text-align: center;
}
.locationHead {
  font-size: 2rem;
  font-weight: bold;
  display: inline-block;
}
.locationCountryCode {
  display: inline-block;
  font-size: 1.1rem;
  opacity: 0.85;
}
.weather-description {
  margin-top: 8px;
}
.sunMoonTimes {
  margin-top: 1rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  gap: 12px;
}
.sunMoonTimes div {
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(8px);
  border-radius: 10px;
  padding: 12px;
  text-align: center;
}
.todayOverview {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 15px 0;
  padding: 16px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 14px;
}
.todayHead {
  color: #fff;
  margin-bottom: 10px;
}
.tempCards {
  display: flex;
  gap: 12px;
  padding-bottom: 8px;
  overflow-x: auto;
}
.tempCard {
  background: inherit;
  padding: 12px;
  text-align: center;
  color: #fff;
  border-radius: 10px;
  min-width: 90px;
  flex-shrink: 0;
  transition: transform 0.2s ease;
}
.tempCard:hover {
  transform: translateY(-3px);
}
.tempCard i {
  font-size: 22px;
  margin: 5px 0;
}
.humidityWind {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}
.humidityWind i {
  font-size: 16px;
  margin-right: 4px;
}
.humidityWind div {
  padding: 12px;
  border-radius: 8px;
  text-align: center;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.06);
}
.weeklyForecast {
  display: grid;
  gap: 1rem;
}
.dayCard {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  align-items: center;
  text-align: center;
  font-size: 14px;
  padding: 12px;
  border-radius: 12px;
  background: #fff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.06);
  transition: transform 0.2s ease;
}
.dayCard:hover {
  transform: translateY(-4px);
}
.dayCard i {
  font-size: 1.8rem;
  margin: 0.5rem 0;
  color: #a0a0a0;
}
.dayCard strong {
  color: #dc3545;
}
.dayCard p:last-child {
  color: #6c757d;
  font-size: 12px;
}
.wi-sunrise,
.wi-sunset {
  color: #ffb347;
}
footer {
  background-color: #fff;
  width: 100%;
  text-align: center;
  font-size: 14px;
  padding: 1rem 0;
  color: #555;
  border: 1px solid #ddd;
}
footer a {
  color: #0366d6;
  text-decoration: none;
}
footer a:hover {
  text-decoration: underline;
}
@media (max-width: 1000px) {
  #app {
    width: 100%;
    grid-template-columns: 1fr;
    padding: 0;
    overflow-y: auto;
  }
  .currentForecast {
    border-radius: 0;
  }
  .weeklyForecast {
    margin-bottom: 15px;
    padding: 0 15px;
  }
  #location {
    font-size: 12px;
  }
  .welcome-message {
    margin-bottom: 20px;
  }
}
@media (max-width: 675px) {
  .tempCard:nth-child(2n) {
    display: none;
  }
  .sunMoonTimes {
    grid-template-columns: 1fr 1fr;
  }
  .sunMoonTimes > div:last-child {
    grid-column: 1/3;
  }
}
@media (max-width: 445px) {
  .right {
    justify-content: center;
  }
  #weather-form h1 {
    font-size: 30px;
  }
  .tempCard {
    min-width: 70px;
  }
  .locationHead {
    font-size: 26px;
  }
  .locationCountryCode {
    font-size: 14px;
  }
  .currentForecast,
  .dayCard,
  footer {
    font-size: 12px;
  }
}
@media (max-width: 345px) {
  .sunMoonTimes {
    grid-template-columns: 1fr;
  }
  .sunMoonTimes > div:nth-child(2),
  .sunMoonTimes > div:nth-child(1) {
    grid-column: 1/3;
  }
  .humidityWind div {
    display: flex;
    flex-direction: column;
  }
}
