dding-box, border-box;\n}\n#location:focus {\n  outline: none;\n  background-image:\n    linear-gradient(white, white), linear-gradient(to right, #8ff470, #fc8561);\n}\n.input-wrapper {\n  position: relative;\n}\n#search-btn {\n  background: #e59766;\n  border: none;\n  border-radius: 8px;\n  padding: 8px 10px;\n  cursor: pointer;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  transition:\n    transform 0.2s ease,\n    background 0.3s ease;\n}\n#search-btn:hover {\n  background: #e67348;\n  transform: scale(1.06);\n}\n#search-btn svg {\n  width: 20px;\n  height: 20px;\n}\n.unit-toggle {\n  position: relative;\n  width: 70px;\n  height: 34px;\n}\n\n.unit-toggle input {\n  display: none;\n}\n\n.slider {\n  position: absolute;\n  cursor: pointer;\n  background-color: #66c569;\n  border-radius: 34px;\n  width: 100%;\n  height: 100%;\n  transition: background-color 0.3s;\n  display: flex;\n  align-items: center;\n  justify-content: space-around;\n  box-sizing: border-box;\n  font-size: 14px;\n  font-weight: bold;\n  color: #ffffff;\n}\n.slider::before {\n  content: \"\";\n  position: absolute;\n  height: 26px;\n  width: 26px;\n  background-color: #2e7d32;\n  color: white;\n  font-size: 12px;\n  font-weight: bold;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  border-radius: 50%;\n  left: 5.15px;\n  bottom: 4.5px;\n  transition:\n    transform 0.3s,\n    background-color 0.3s;\n  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);\n}\n.unit-text {\n  z-index: 2;\n  transition: color 0.3s;\n}\n.unit-text.celsius {\n  color: #ffffff;\n}\n.unit-text.fahrenheit {\n  color: rgba(255, 255, 255, 0.6);\n}\n.unit-toggle input:checked + .slider {\n  background-color: #2196f3;\n}\n.unit-toggle input:checked + .slider::before {\n  transform: translateX(34px);\n  background-color: #1565c0;\n}\n.unit-toggle input:checked + .slider .unit-text.celsius {\n  color: rgba(255, 255, 255, 0.6);\n}\n.unit-toggle input:checked + .slider .unit-text.fahrenheit {\n  color: #ffffff;\n}\n\n#app {\n  flex: 1;\n  display: grid;\n  grid-template-columns: 2fr 1fr;\n  justify-content: space-around;\n  width: 100%;\n  gap: 24px;\n  padding: 24px;\n}\n.welcome-message {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  grid-column: 1/3;\n  width: 100%;\n  text-align: center;\n  padding: 0 5px;\n  color: #555;\n  transition: all 0.3s ease-in-out;\n}\n.welcome-gif {\n  width: 250px;\n  height: 250px;\n}\n.welcome-message h2 {\n  font-family: \"Montserrat\", sans-serif;\n  font-size: 1.8rem;\n  margin-bottom: 10px;\n  color: #333;\n}\n\n.welcome-message p {\n  font-family: \"Montserrat\", sans-serif;\n  font-size: 1rem;\n  color: #666;\n}\n.currentForecast {\n  background-color: rgba(0, 0, 0, 0.6);\n  background-blend-mode: darken;\n  backdrop-filter: blur(12px);\n  display: flex;\n  justify-content: space-around;\n  flex-direction: column;\n  font-size: 14px;\n  border-radius: 10px;\n  transition: background-image 0.6s ease-in-out;\n}\n.location {\n  text-align: center;\n}\n.locationHead {\n  font-size: 2rem;\n  font-weight: bold;\n  display: inline-block;\n}\n.locationCountryCode {\n  display: inline-block;\n  font-size: 1.1rem;\n  opacity: 0.85;\n}\n.weather-description {\n  margin-top: 8px;\n}\n.sunMoonTimes {\n  margin-top: 1rem;\n  display: grid;\n  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));\n  gap: 12px;\n}\n.sunMoonTimes div {\n  background: rgba(255, 255, 255, 0.2);\n  backdrop-filter: blur(8px);\n  border-radius: 10px;\n  padding: 12px;\n  text-align: center;\n}\n.todayOverview {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  margin: 15px 0;\n  padding: 16px;\n  background: rgba(255, 255, 255, 0.2);\n  border-radius: 14px;\n}\n.todayHead {\n  color: #fff;\n  margin-bottom: 10px;\n}\n.tempCards {\n  display: flex;\n  gap: 12px;\n  padding-bottom: 8px;\n  overflow-x: auto;\n}\n.tempCard {\n  background